@import 'colors';
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    #video {
        position: fixed;
        z-index: -2;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    #black-back {
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-color: $back;
        opacity: 0.8;
    }
    .top-menu {
        position: fixed;
        top: 0;
        width: 100%;
        .top-btns-wrapper {
            float: right;
            width: fit-content;
            padding-top: 20px;
            padding-right: 20px;
            button {
                color: #9e9e9e !important;
                font-weight: 600;
                font-size: 16px;
                text-decoration: none;
                cursor: pointer;
                &:hover {
                    color: rgb(206, 206, 206) !important;
                }
            }
        }
    }
    .mt-flex-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 100vh;
        div {
            text-align: center;
            h1 {
                font-family: 'Pacifico', cursive;
                font-size: 60px;
                color: $logoText;
                line-height: 1em;
                margin-bottom: 40px;
                text-shadow: 2px 2px 4px #750000;
            }
            p {
                font-family: 'Poppins', sans-serif;
                font-size: 18px;
                line-height: 1.5em;
                color: #e2e2e2;
                font-weight: 300;
                margin-bottom: 50px;
            }
            .ghost-btn {
                font-family: 'Poppins', sans-serif;
                display: inline-block;
                width: 200px;
                padding: 8px;
                color: #fff;
                border: 1px solid #fff;
                text-align: center;
                outline: none;
                text-decoration: none;
                transition: background-color 0.2s ease-out, color 0.2s ease-out;
                border-radius: 5px;
                &:active,
                &:hover {
                    background-color: #fff;
                    color: #000;
                    transition: background-color 0.3s ease-in, color 0.3s ease-in;
                }
            }
        }
    }
    .modal-content {
        background-color: #000 !important;
        color: #e2e2e2 !important;
        .modal-header {
            font-family: 'Pacifico', cursive;
            font-size: 30px;
            border-bottom: none !important;
            color: #c19b76 !important;
        }
        .modal-body {
            color: #d4d4d4 !important;
            font-weight: 300;
            h6 {
                color: #c19b76 !important;
                span {
                    color: #d4d4d4 !important;
                }
            }
        }
        .authors {
            padding-top: 0px;
            padding-bottom: 20px;
        }
        button {
            color: #FFF !important;
        }
    }
}